<template>
	<div class="check_page2">
		<div class="cont box-border-shadow">
			<el-row :gutter="20">
			  <el-col :span="24">
			  	<div class="grid-content">
			  		<div class="inputBox">
			  			<el-col :span="12" style="position: relative; margin-top: 20px;padding-left: 55px;">
			  				<span style="position: absolute;left: 0; display:inline-block;color: #b80000;font-size: 18px;line-height: 38px;">实体1:</span>
			  				<el-autocomplete style="width: 100%" v-model="form[0].text" 
			  				  :fetch-suggestions="querySearchAsync"
							  placeholder="请输入内容"
							  clearable>
							   <template slot="prepend">{{form[0].leibie}}</template>
							</el-autocomplete>
			  			</el-col>
			  			<el-col :span="12" style="position: relative; margin-top: 20px;padding-left: 55px;">
			  				<span style="position: absolute;left: 0; display:inline-block;color: #b80000;font-size: 18px;line-height: 38px;">实体2:</span>
			  				<el-autocomplete style="width: 100%" v-model="form[1].text" 
			  				  :fetch-suggestions="querySearchAsync2"
							  placeholder="请输入内容"
							  clearable>
							   <template slot="prepend">{{form[1].leibie}}</template>
							</el-autocomplete>
			  			</el-col>
			  			<el-col :span="12" style="position: relative; margin-top: 20px;padding-left: 55px;">
			  				<span style="position: absolute;left: 0; display:inline-block;color: #b80000;font-size: 18px;line-height: 38px;">实体3:</span>
			  				<el-autocomplete style="width: 100%" v-model="form[2].text" 
			  				  :fetch-suggestions="querySearchAsync3"
							  placeholder="请输入内容"
							  clearable>
							   <template slot="prepend">{{form[2].leibie}}</template>
							</el-autocomplete>
			  			</el-col>
			  			<el-col :span="12" style="position: relative; margin-top: 20px;padding-left: 55px;">
			  				<span style="position: absolute;left: 0; display:inline-block;color: #b80000;font-size: 18px;line-height: 38px;">实体4:</span>
			  				<el-autocomplete style="width: 100%" v-model="form[3].text" 
			  				  :fetch-suggestions="querySearchAsync4"
							  placeholder="请输入内容"
							  clearable>
							   <template slot="prepend">{{form[3].leibie}}</template>
							</el-autocomplete>
			  			</el-col>
			  		</div>
			  		<div class="selectBox">
			  			<span style="position: absolute;left: 0;color: #b80000;font-size: 18px;line-height: 38px;">关系:</span>
			  			<el-select style="width: 100%" v-model="relativeSelect" filterable placeholder="请选择" multiple>
						    <el-option
						      v-for="item in relativeOptions"
						      :key="item.value"
						      :label="item.label"
						      :value="item.value">
						    </el-option>
						</el-select>
			  		</div>
			  		<div class="searchBtnBox">
			  			<el-button class="searchBtn" icon="el-icon-search" plain @click="searchBtn"> 检索</el-button>
			  		</div>
			  		<div style="margin:10px auto;text-align: center;">
					    <el-radio v-model="radio" label="1" border>知识图谱</el-radio>
					    <el-radio v-model="radio" label="2" border>原始数据</el-radio>
					</div>
			  		<div v-show="radio=='1'" class="EChartsBox">
			  			<div v-show="isData" style="position: relative;">
						<div id="graphCharts" :style="{width:windowWidth+'px'}" style="height: 800px;"></div>
						</div>
						<div v-show="!isData">
							<img style="display:block;margin: 50px auto;" src="../../../assets/no-sj.png">
						</div>
			  		</div>
			  		<div v-show="radio=='2'" class="listBox">
			  			<table cellpadding="0" cellspacing="0" class="table_list">
			  				<thead>
			  					<tr>
			  						<th v-for="item in listBoxData.ziduan_name_list">{{item}}</th>
			  					</tr>
			  				</thead>
			  				<tbody>
			  					<tr v-for="items in listBoxData.ziduan_shuju_list">
			  						<td v-for="item in items">{{item}}</td>
			  					</tr>
			  				</tbody>
			  			</table>
			  		</div>
			  	</div>
			  </el-col>
			</el-row>
		</div>
	</div>
</template>
<script type="text/javascript">
	import Echarts from '../Echarts'
    import * as echarts from 'echarts'
	import reqData from "./../../../reqData.js"
	export default{
		name:'check_page2',
		data(){
			return{
				radio: '1',
				showAddInputBtn:true,
				form:[
					{
						text:'',
						leibie:'科目名称',
						index:'0'
					},{
						text:'',
						leibie:'借方金额',
						index:'1'
					},{
						text:'',
						leibie:'负责人',
						index:'2'
					},{
						text:'',
						leibie:'管理部门',
						index:'3'
					}
				],
				typeOption:[{
		          value: '0',
		          label: '科目名称'
		        }, {
		          value: '1',
		          label: '借方金额'
		        }, {
		          value: '2',
		          label: '负责人'
		        }, {
		          value: '3',
		          label: '管理部门'
		        }],
				relativeOptions: [{
		          value: '借方金额数值',
		          label: '借方金额数值'
		        }, {
		          value: '负责人姓名',
		          label: '负责人姓名'
		        }, {
		          value: '负责人所属部门',
		          label: '负责人所属部门'
		        }, {
		          value: '负责借方金额数值',
		          label: '负责借方金额数值'
		        }, {
		          value: '管理借方金额数值',
		          label: '管理借方金额数值'
		        },{
		          value: '隶属于',
		          label: '隶属于'
		        }],
		        relativeSelect: '',
				isData:true,
				timeout:null,
				restaurants:[],
				listBoxData:{},
				windowWidth:(window.innerWidth)*0.85,
			}
		},
		components:{
			Echarts
		},
		computed:{
			userName(){
				if(sessionStorage.getItem('username')){
					return sessionStorage.getItem('username');
				}
			},
		},
		methods: {
			loadAll(index,data,cb) {
				console.log(data);
		        var result=[];
			 	reqData
			      .req({
			        apiName: "nanshen_caiwu_sql_chaxun_tuijian",
			        data: {			        
			            text:data?data:',',
					    leibie:index,
			        }
			      })
			      .then(res => {
			        res = res.data;
			        if (res && res.code === "200") {
			          let data = res.data;
			          console.log(data);
			          for(var i=0;i<data.length;i++){
			          	// result.push({value:data[i][0],id:data[i][1]});
			          	result.push({value:data[i][0]});
			          }
			          console.log(result);
			          this.restaurants=result;
			          cb(result);
			        }
			      })
			      .catch(err => {
			        console.log(err);
			      });
		    },
			querySearchAsync(queryString, cb) {
		    	this.loadAll(0,queryString,cb);
		    },
		    querySearchAsync2(queryString, cb) {
		    	this.loadAll(1,queryString,cb);
		    },
		    querySearchAsync3(queryString, cb) {
		    	this.loadAll(2,queryString,cb,);
		    },
		    querySearchAsync4(queryString, cb) {
		    	this.loadAll(3,queryString,cb);
		    },
		    createStateFilter(queryString) {
		        return (state) => {
		          return (state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
		        };
		    },
			addInput(){
				if(this.form.length<4){
					this.form.push({
						text:'',
						leibie:''
					});
				}
				if(this.form.length==4){
					this.showAddInputBtn=false;
				}
			},
			unique(arr,links){
				var newArr = [arr[0].name];
		   		var data=[arr[0]];
		   		//去重复项
		  　　 	for(var i=1;i<arr.length;i++){
		　　　　		if(newArr.indexOf(arr[i].name) == -1){
		          　　  newArr.push(arr[i].name);
		          	    data.push(arr[i]);
		    　　  	}
	    		}
	    		let _this=this;
	    		//区分值和主题
	    		for(var i=0;i<data.length;i++){
	    			if(data[i].des&&data[i].des==='0'){
	    		 		data[i].category=_this.typeOption[0].label;
	    		 		data[i].itemStyle={
				             normal: {
				                 color: '#ff756e',
				                 borderColor:'#e06760',
				                 borderWidth:2,
				             }
				         }
	    		 	}else if(data[i].des&&data[i].des==='1'){
	    		 		data[i].category=_this.typeOption[1].label;
	    		 		data[i].itemStyle={
				             normal: {
				                 color: '#68bdf6',
				                 borderColor:'#5ca8db',
				                 borderWidth:2,
				             }
				         }
	    		 	}else if(data[i].des&&data[i].des==='2'){
	    		 		data[i].category=_this.typeOption[2].label;
	    		 		data[i].itemStyle={
				             normal: {
				                 color: '#ffd86e',
				                 borderColor:'#edba39',
				                 borderWidth:2,
				             }
				         }
	    		 	}else if(data[i].des&&data[i].des==='3'){
	    		 		data[i].category=_this.typeOption[3].label;
	    		 		data[i].itemStyle={
				             normal: {
				                 color: '#6dce9e',
				                 borderColor:'#60b58b',
				                 borderWidth:2,
				             }
				         }
	    		 	}
	    		}
	    		console.log(data);
		        return data;
		  },
		  searchBtn(){
		  	let formData=[];
		  	this.form.forEach((value,index)=>{
		  		if(value.text){
		  			formData.push({text:value.text,leibie:value.index});
		  		}
		  	});
		  	formData=formData.length>0?formData:[{text:'',leibie:''}];
		  	let shiti=JSON.stringify(formData);
			let guanxi=JSON.stringify(this.relativeSelect);
			console.log(shiti);
			console.log(guanxi);
		  	 this.isData=true;
		 	 var graphCharts = echarts.init(document.getElementById('graphCharts'));
		 	 graphCharts.showLoading();
		 	 reqData
		        .req({
		          apiName: "nanshen_caiwu_sql_chaxun_zhishitupu",
		           data: {             
		             shiti:shiti,
					 guanxi:guanxi,
		           }
		        })
		        .then(res => {
		          res = res.data;
		          if (res && res.code === "200") {
		            let data = res.data;
		            console.log(data);
		            this.listBoxData={
		            	ziduan_name_list:res.ziduan_name_list,
		            	ziduan_shuju_list:res.ziduan_shuju_list
		            }
		            let nodes=data.node;
					let links=data.links;
					if(nodes.length>0){
				    	console.log(nodes);
						nodes=this.unique(nodes);
						// links=this.linksFormat(links);
						graphCharts.hideLoading();
				    	graphCharts.setOption(this.graphOption(nodes,links)); 
			    	    $(window).resize(function(){
							graphCharts.resize({
								width:(window.innerWidth)*0.85,
								height:'auto'
							})
					    })
					}else{
						this.isData=false;
					}
		          }
		        })
		        .catch(err => {
		          console.log(err);
		          alert("接口异常");
		       });
		  },
		  graphOption(nodes,links){
		    	console.log(nodes);
		    	console.log(links);
		    	let _this=this;
		    	let option = {
				     title: {
				         text: ''
				     },
				     tooltip: {
				         formatter: function(x) {
				             return x.data.name;
				         }
				     },
				      label: {
				        normal: {
				            show: true,
				            textStyle: {
				                fontSize: 22
				            },
				        }
				    },
				     legend: {
				        x: "center",
				        textStyle:{
				        	fontSize:16,
				        },
				        data: [
				        {name:_this.typeOption[0].label,icon:'circle'},
				        {name:_this.typeOption[1].label,icon:'circle'}, 
				        {name:_this.typeOption[2].label,icon:'circle'}, 
				        {name:_this.typeOption[3].label,icon:'circle'},
				      	]
				    },
				     series: [{
				         type: 'graph',
				         layout: 'force',
				         focusNodeAdjacency: true,
				         symbolSize: 60,
				         roam: true,
				         categories: [{
			                name: _this.typeOption[0].label,
			                itemStyle: {
				                 color: '#ff756e',
				                 borderColor:'#e06760',
				                 borderWidth:2,
				            }
			             }, {
			                name: _this.typeOption[1].label,
			                itemStyle: {
				                 color: '#68bdf6',
				                 borderColor:'#5ca8db',
				                 borderWidth:2,
				            }
			             }, {
			                name: _this.typeOption[2].label,
			                itemStyle: {
				                 color: '#ffd86e',
				                 borderColor:'#edba39',
				                 borderWidth:2,
				            }
			             }, {
			                name: _this.typeOption[3].label,
			                itemStyle: {
				                 color: '#6dce9e',
				                 borderColor:'#60b58b',
				                 borderWidth:2,
				            }
			             }],
				         edgeSymbol: ['none', 'arrow'],
				         // edgeSymbolSize: [80, 10],
				         hoverAnimation:true,
				         force: {
				         	initLayout:'circular',
				             repulsion: 2000,
            				 layoutAnimation:false,
            				 gravity:0.5,
				             edgeLength: [16, 30],
				             layoutAnimation:true
				         },
				         draggable: true,
				         lineStyle: {
				             normal: {
				                 width: 1,
				                 color: '#000',
				                 curveness:0.2
				             }
				         },
				         edgeLabel: {
				             normal: {
				                 show: true,
				                 textStyle: {fontSize: 18,color:'#000'},
				                 formatter: function(x) {
				                 	let name=x.data.name;
						         	if(name.length>5){
										name=name.substr(0,5)+'...';
										// v.nameSub=name;
									}else{
										name=name.substr(0,5);
										// v.nameSub=name;
									}
				                     return name;
				                 }
				             }
				         },
				         label: {
				         	show:true,
				            formatter: function(x) {
			                 	let name=x.data.name;
					         	if(name.length>3){
									name=name.substr(0,3)+'...';
									// v.nameSub=name;
								}else{
									name=name.substr(0,3);
									// v.nameSub=name;
								}
			                     return '{a|'+name+'}';
			                },
			                rich: {
						        a: {
						            color: '#000',
						            fontSize: 18,
						        },
						    }
				         },
				        nodes: nodes, 
						links:links 
				    }]
				};
				return option;
		    },
	    },
	    mounted(){
	    	// this.loadAll({leibie: "0",text: ","});
	    }

	}
</script>
<style type="text/css" scoped="">
	.check_page2 .cont{
		width: 90%;
	    margin: 30px auto;
	    min-height: 600px;
	    /*padding: 40px;*/
	    box-sizing: border-box;
	} 
	.check_page2 .cont .inputBox,
	.check_page2 .cont .selectBox,
	.check_page2 .cont .searchBtnBox{
		/*position: relative;*/
		margin: 0 auto;
		width: 50%;
		overflow: hidden;
		/*text-align: center;*/
	}
	.check_page2 .cont .selectBox{
		margin-top: 30px;
		position: relative;
		padding-left: 55px;
		box-sizing: border-box;
	}
	.check_page2 .addInputBtn{
		/*position: absolute;*/
	    /*bottom: 0;*/
	    /*right: -70px;*/
	    font-size: 20px;
	    padding: 0;
	    line-height: 38px;
	    height: 38px;
	    width: 60px;
	    margin-top: 20px;
	}
	.check_page2 .searchBtn{
		float: right;
		margin-top: 20px;
		font-size: 16px;
	}
	.check_page2 .el-select .el-input {
	  width: 130px;
	}
	.check_page2 .dec{
		width: 50%;
		margin: 0 auto;
		height: 44px;
		white-space: nowrap;
	    overflow: hidden;
	    text-overflow: ellipsis;
	}
	.check_page2 .dec li{
		float: left;
		margin: 10px 0;
		color: #999;
	}
	.check_page2 .dec li.item{
		margin-right: 10px;
		cursor: pointer;
	}
	.check_page2 .dec li.item:hover{
		color: #b80000;
	}
	.check_page2 .cont .table_list{
		width: 80%;
		margin: 0 auto 20px;
	}
	.check_page2 .cont .table_list td,
	.check_page2 .cont .table_list th{
		border: 1px solid #ccc;
		text-align: center;
		line-height: 40px;
	}
	.check_page2 .cont .table_list th{
		border: none;
		background-color: rgb(184,0,0,0.7);
		color: #fff;
		line-height: 44px;
	}
</style>


